// import dependencies
import './_alert.scss'
import '../buttons/_buttons.scss'
import template from './alert.html'

// export component object
export default {
    template: template,
    replace: true,
    computed: {
    	alertState: function alertState() {
    		return !this.state || this.state === 'default' ? `alert-success` : `alert-${this.state}`
    	}
    },
    props: {
        show: {
            type: Boolean,
            default: false,
            required: true
        },
        message: {
            type: String,
            default: '',
            required: true
        },
        state: {
            type: String,
            default: 'success'
        },
        dismissible: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        dismiss: function dismiss() {
            // hide an alert
            this.show = false
            // Dispatch an event from the current vm that propagates all the way up to its $root
            this.$dispatch('dismiss::alert')
        }
    }
}



